import { Component, OnInit } from '@angular/core';
import { ClientService, Client, Domain } from '../services/client.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';

class Errors {
  constructor(
    public domain?: string,
    public non_field_errors?: string,
  ) {}
}

@Component({
  selector: 'app-domain',
  templateUrl: './domain.component.html',
  styleUrls: ['./domain.component.css']
})

export class DomainComponent implements OnInit {
  domain: Domain = new Domain();
  error: Errors = new Errors();
  focus: any;
  focus1: any;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private clientService: ClientService
  ) {
    route.params.subscribe(params => { this.domain.tenant = params['id']; });
    this.domain.is_primary = true
   }

  ngOnInit(): void {
  }

  createDomain(): void {
    this.clientService.createDomain(
      this.domain.domain,
      this.domain.is_primary,
      this.domain.tenant     
    ).subscribe((res) => {
      this.router.navigateByUrl('/super-admin-signup/' + `${res.domain}`);
      console.log(res);
      console.log("successfully created");
      
    }, (error) => {
      console.error(error);
      this.error = error.error;
    });

  }

}
