import { HttpClient} from '@angular/common/http';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { finalize, tap } from 'rxjs/operators';
import { GlobalService } from '../services/global.service';
import { environment } from '../../../environments/environment';
export class User {
  constructor(
    public id?: number,
    public username?: string,
    public full_name?: string,
    public email?: string,
    public user_subdomain?: string,
    public phone_number?: string,
    public group?: string
  ) {}

  static create(data: any): User {
    return new User(
      data.id,
      data.username,
      data.full_name,
      data.email,
      data.user_subdomain,
      data.phone_number,
      data.group
    );
  }

  static getUser(): User {
    const userData = localStorage.getItem('tenant.user');
    if (userData) {
      console.log(JSON.parse(userData));
      return User.create(JSON.parse(userData));
    }
    return null;
  }

  static isUser(): boolean {
    const user = User.getUser();
    if (user === null) {
      return false;
    }
    return true;
  }

  static isStudent(): boolean {
    const user = User.getUser();
    if (user === null) {
      return false;
    }
    console.log(user);
    return user.group === 'STUDENT';
  }

  static isOwner(): boolean {
    const user = User.getUser();
    if (user === null) {
      return false;
    }
    return user.group === 'OWNER' || user.group === 'SCHOOL-SUPER-ADMIN' ;
  }

  static isParent(): boolean {
    const user = User.getUser();
    if (user === null) {
      return false;
    }
    return user.group === 'PARENT';
  }

  static isTeacher(): boolean {
    const user = User.getUser();
    if (user === null) {
      return false;
    }
    console.log(user.group);
    return user.group === 'TEACHER';
  }

  static isSchoolManager(): boolean {
    const user = User.getUser();
    if (user === null) {
      return false;
    }
    console.log(user.group);
    return user.group === 'SCHOOL-MANAGER';
  }

  static isReceptionist(): boolean {
    const user = User.getUser();
    if (user === null) {
      return false;
    }
    console.log(user.group);
    return user.group === 'RECEPTIONIST';
  }

  static isAccountant(): boolean {
    const user = User.getUser();
    if (user === null) {
      return false;
    }
    console.log(user.group);
    return user.group === 'ACCOUNTANT';
  }
  // ,'SCHOOL-SUPER-ADMIN', 'STUDENT', 'SCHOOL-MANAGER', 'HR-MANAGER', 'RECEPTIONIST', 'TEACHER', 'LIBRARIAN', 'PARENT', 'FRONT-DESK', 'ACCOUNTANT'
  static isHrManager(): boolean {
    const user = User.getUser();
    if (user === null) {
      return false;
    }
    console.log(user.group);
    return user.group === 'HR-MANAGER';
  }

  static isLibrarian(): boolean {
    const user = User.getUser();
    if (user === null) {
      return false;
    }
    console.log(user.group);
    return user.group === 'LIBRARIAN';
  }

  static isSuperAdmim(): boolean {
    const user = User.getUser();
    if (user === null) {
      return false;
    }
    console.log(user.group);
    return user.group === 'SCHOOL-SUPER-ADMIN';
  }
  

}

