@Injectable({
  providedIn: 'root'
})
export class ClientService {
  API_URL = environment.apiUrl;
  DOMAIN_URL = environment.clientapiUrl;
  PROTOCOL = "http://"
  baseurl = this.API_URL;
  domainurl = this.DOMAIN_URL;
  protocol = this.PROTOCOL;

  constructor(
    private http: HttpClient,
    private globalService: GlobalService
  ) { }

  createClient(
    schema_name: string,
    name: string,
    description: string,
  ): Observable<Client> {
    const url = this.baseurl + '/api/client/';
    const formData = new FormData();
    formData.append('schema_name', schema_name);
    formData.append('name', name);
    formData.append('description', description);
    return this.http.request<Client>('POST', url, {body: formData});
  }

  createDomain(
    domain: string,
    is_primary: any,
    tenant: string,
  ): Observable<Domain> {
    const url = this.baseurl + '/api/client/domain/';
    const formData = new FormData();
    formData.append('domain', domain);
    formData.append('is_primary', is_primary);
    formData.append('tenant', tenant);
    return this.http.request<Domain>('POST', url, {body: formData});
  }
  schoolSuperAdminSignup(
    // domain: string,
    full_name: string,
    email_address: string,
    // user_subdomain: string,
    phone_number: string,
    password1: string,
    password2: string,
    group: string
  ): Observable<Signup> {
    const url = this.baseurl + '/api/create_user_by_tech_oga/';
    // const url = 'http://' + domain +'/create_user_by_tech_oga/';
    const formData = new FormData();
    formData.append('username', email_address);
    formData.append('email_address', email_address);
    formData.append('full_name', full_name);
    formData.append('phone_number', phone_number),
    formData.append('password1', password1);
    formData.append('password2', password2);
    formData.append('group', group);
    return this.http.request<Signup>('POST', url, {body: formData});
  }

  studentUserSignup(
    domain: string,
    full_name: string,
    email_address: string,
    // user_subdomain: string,
    phone_number: string,
    password1: string,
    password2: string,
    group: string
  ): Observable<Signup> {
    // const url = this.baseurl + '/create_user_by_tech_oga/';
    const url = 'http://' + domain +'.localhost/api/create_user_by_tech_oga/';
    const formData = new FormData();
    formData.append('username', email_address);
    formData.append('email', email_address);
    formData.append('full_name', full_name);
    formData.append('phone_number', phone_number),
    formData.append('password1', password1);
    formData.append('password2', password2);
    formData.append('group', group);
    return this.http.request<Signup>('POST', url, {body: formData});
  }

  filterUserSubDomain(
    username: string,
  ): Observable<any[]> {
    // const url = this.baseurl + '/create_user_by_tech_oga/';
    const url = this.baseurl + '/api/users/?username=' + username;
    // const formData = new FormData();
    // formData.append('username', username);
    return this.http.request<any[]>('GET', url,);
  }


  ownerLogIn(
    username: string, 
    password: string): Observable<Login> {
    const url =  this.baseurl + '/api/login/';
    return this.http.post<Login>(url, {username, password}).pipe(
      tap(user => localStorage.setItem('tenant.user', JSON.stringify(user)))
    );
  }

  usersLogIn(
    sub_domain: string,
    username: string, 
    password: string): Observable<Login> {
    // const url = this.baseurl + '/login/';
    const url = this.PROTOCOL + sub_domain + '.localhost/api/login/';
    console.log("doesnt work")
    return this.http.post<Login>(url, {username, password},
      {headers: this.globalService.headers()}).pipe(
      tap(user => localStorage.setItem('tenant.user', JSON.stringify(user.data)))

     
    );
  }

  editUserSubdomain(
    user_id: any,
    user_subdomain

  ): Observable<User> {
    const url = this.baseurl + '/api/updateOwnerSubDomain/' + user_id;
    const formData = new FormData();  
    formData.append('user_subdomain', user_subdomain);
    return this.http.request<Signup>('PUT', url, {body: formData});
  }

  logOut(): Observable<any> {
    const url = '/api/log_out/';
    return this.http.post(url, null).pipe(
      finalize(() => localStorage.removeItem('tenant.user'))
    );
  }

  
}
