import { Injectable } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';
import { HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';
@Injectable({
  providedIn: 'root'
})
export class GlobalService {
  apiHost = environment.apiUrl;
  DOMAIN_URL = environment.clientapiUrl;
  PROTOCOL = "http://"
  baseurl = this.apiHost;
  domainurl = this.DOMAIN_URL;
  protocol = this.PROTOCOL;

  constructor(
    private cookieService: CookieService
  ) { }

  API_URL = 'http://127.0.0.1:8000/';
  httpHeaders = {'Content-Type': 'application/json'};

  headers() {
    const token = this.getToken();

    if (token !== '') {
      this.httpHeaders['Authorization'] = 'Token ' + token;

    }

    return new HttpHeaders(this.httpHeaders);
  }

  setToken(token: string) {
    this.cookieService.set('ATN', token);
  }

  isAuthenticated() {
    const token = this.getToken();
    return token === '' ? false : true;
  }

  getToken() {
    return this.cookieService.get('ATN');
  }

}
