import { Component, OnInit } from '@angular/core';
import { ClientService, Client, User } from '../services/client.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';

class Errors {
  constructor(
    public schema_name?: string,
    public name?: string,
    public description?: string,
    public non_field_errors?: string,
  ) {}
}

@Component({
  selector: 'app-client',
  templateUrl: './client.component.html',
  styleUrls: ['./client.component.css']
})

export class ClientComponent implements OnInit {
  user: User = new User();
  client: Client = new Client();
  error: Errors = new Errors();
  success: any = "";
  focus: any;
  focus1: any;

  constructor(private route: ActivatedRoute,
    private router: Router,
    private clientService: ClientService) {
      route.params.subscribe(params => { this.user.id = params['id']; });
      
     }
     

  ngOnInit() {}

  createClient(): void {
    this.clientService.createClient(
      this.client.schema_name,
      this.client.name,
      this.client.description     
    ).subscribe((res) => {
      // this.router.navigateByUrl('/create-domain/' + `${res.id}`);
      console.log("client successfully created");
      this.createDomain(res.id)
      console.log(res);
      
      
    }, (error) => {
      console.error(error);
      this.error = error.error;
    });

  }

  createDomain(tenant): void {
    this.clientService.createDomain(
      this.client.schema_name + ".localhost",
      "true",
      tenant    
    ).subscribe((res) => {
      // this.router.navigateByUrl('/super-admin-signup/' + `${res.domain}`);
      console.log("domain successfully created");
      this.editUserSubdomain();
      console.log(res);
      
      
    }, (error) => {
      console.error(error);
      this.error = error.error;
    });

  }

  editUserSubdomain(): void {
    this.clientService.editUserSubdomain(
      // user_subdomain,
      this.user.id,
      this.client.schema_name 
    ).subscribe((res) => {
      this.router.navigateByUrl('/create/done/' + `${this.client.name}`);
      this.success = `${this.client.name} instance has been successfully created`;
      console.log("successfully updated user subdomain")

      console.log(res);
      // console.log("successfully logged in");
      // this.success = res;
      // this.success = "Your super admin account has been successfully created"
      
    }, (error) => {
      console.error(error);
      // this.error = error.error;
    });

  }

}
