import { Component, OnInit } from '@angular/core';
import { ClientService, Client, Domain, Signup } from '../services/client.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';

class Errors {
  constructor(
    public schema_name?: string,
    public name?: string,
    public description?: string,
    public non_field_errors?: string,
  ) {}
}

@Component({
  selector: 'app-super-admin-signup',
  templateUrl: './super-admin-signup.component.html',
  styleUrls: ['./super-admin-signup.component.css']
})
export class SuperAdminSignupComponent implements OnInit {
  schoolSuperAdminSignup: Signup = new Signup();
  error: Errors = new Errors();
  success: string = '';
  focus: any;
  focus1: any;
  focus2: any;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private clientService: ClientService
  ) {
    route.params.subscribe(params => { this.schoolSuperAdminSignup.domain = params['id']; });
    this.schoolSuperAdminSignup.group = 'OWNER'
   }

  
  ngOnInit(): void {
  }

  schoolSuperadminSignup(): void {
    this.clientService.schoolSuperAdminSignup(
      // this.schoolSuperAdminSignup.domain,
      this.schoolSuperAdminSignup.full_name,
      this.schoolSuperAdminSignup.email_address,
      this.schoolSuperAdminSignup.phone_number,
      this.schoolSuperAdminSignup.password1,
      this.schoolSuperAdminSignup.password2,
      this.schoolSuperAdminSignup.group     
    ).subscribe((res) => {
      this.router.navigateByUrl('/login');
      console.log(res);
      console.log("successfully created");
      this.success = "Your super admin account has been successfully created"
      
    }, (error) => {
      console.error(error);
      this.error = error.error;
    });

  }

}
